import React, {Component} from 'react'
import {BrowserRouter as Router, Link} from 'react-router-dom'

import './style.css'

class Header extends Component {
    constructor (props) {
        super(props)
    }

    render () {
        return (
          <aside className="admin-aside">
              <nav className="nav-bar">
                  <ul className="nav-list clearfix">
                      <li className="list-item"><Link to="/"></Link></li>
                      <li className="list-item"><Link to="/admin/category"></Link></li>
                      <li className="list-item"><Link to="/admin/tag"></Link></li>
                      <li className="list-item"><Link to="/admin/article"></Link></li>
                      <li className="list-item"><Link to="/admin/management"></Link></li>
                      <li className="list-item"><a href="/api/logout"></a></li>
                  </ul>
              </nav>
          </aside>
        )
    }
}

module.exports = Header